import { createStore } from "vuex";
import { auth } from "@/plugins/firebase";
import {
  signInWithEmailAndPassword,
  createUserWithEmailAndPassword,
  updateProfile,
} from "firebase/auth";

export default createStore({
  state: {
    userLoggedIn: false,
    user: {},
    theme: "light",
    showModal: false,
  },
  mutations: {
    toggleModal(state) {
      state.showModal = !state.showModal;
    },
    toggleAuth(state) {
      state.userLoggedIn = true;
    },
    SET_THEME(state, theme) {
      state.theme = theme;
      localStorage.theme = theme;
    },
    SET_USER(state, user) {
      state.user = user;
    },
  },
  actions: {
    async register({ commit }, payload) {
      let res = await fetch(
        "https://script.google.com/macros/s/AKfycbyIyZqGoHCZUbsRu8IBo7p116MQXiDvopjxnoawweecvtJC6aCyJl0-zPirQBbSoYI2Rg/exec",
        {
          method: "POST",
          body: JSON.stringify([...Object.values(payload.values)]),
        }
      );
      let data = await res.json();
      const userCred = await createUserWithEmailAndPassword(
        auth,
        payload.values.email,
        payload.values.password
      );

      await updateProfile(userCred.user, { displayName: data.fileid });

      commit("toggleAuth");
      payload.router.push("/");
    },

    async login({ commit }, payload) {
      await signInWithEmailAndPassword(
        auth,
        payload.values.email,
        payload.values.password
      );
      commit("toggleAuth");
      payload.router.push("/");
    },
    async signOut({ commit }, payload) {
      await auth.signOut();
      commit("toggleAuth");
      if (payload.route.meta.requiresAuth) {
        payload.router.push({ name: "login" });
      }
    },
    initTheme({ commit }) {
      const cachedTheme = localStorage.theme ? localStorage.theme : false;
      //  `true` if the user has set theme to `dark` on browser/OS
      const userPrefersDark = window.matchMedia("(prefers-color-scheme: dark)")
        .matches;

      if (cachedTheme) commit("SET_THEME", cachedTheme);
      else if (userPrefersDark) commit("SET_THEME", "dark");
      else commit("SET_THEME", "light");
    },
  },
  getters: {
    getTheme: (state) => {
      return state.theme;
    },
  },
  modules: {},
});
